/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  box-sizing: border-box; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

:root {
  font-family: "Rubik", sans-serif;
  font-size: 16px;
  --Textmenu: #4a4a4a;
  --Colorbackground: #3cdbc0;
  --ColorTextDiversity: #ffffff;
  --FontSize-Mobile: 30px; }

.header {
  padding: 10px 0 20px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: fixed;
  top: 0;
  width: 100%;
  background-color: white; }
  .header__logo {
    width: 155px; }
  .header__menu {
    padding-top: 10px;
    padding-right: 10px;
    padding-left: 10px;
    display: inline-block; }
  .header__link {
    text-transform: uppercase;
    text-decoration: none;
    padding: 10px; }
    .header__link:hover {
      color: var(--Colorbackground);
      text-decoration: none; }
    .header__link:active {
      color: var(--Textmenu);
      text-decoration: none; }
  @media (min-width: 768px) {
    .header {
      flex-direction: row;
      justify-content: space-between;
      padding: 20px 30px; }
      .header__menu {
        padding-top: 0; }
      .header__link {
        margin-left: 20px; } }

.main {
  height: 100vh;
  background-color: var(--Colorbackground);
  padding-top: 60px;
  color: var(--ColorTextDiversity);
  display: flex;
  justify-content: center;
  align-items: center; }
  .main__title {
    display: flex;
    flex-direction: column; }
  .main__item {
    text-align: center;
    font-weight: bold;
    font-size: var(--FontSize-Mobile);
    line-height: 36px;
    text-transform: capitalize; }
  @media (min-width: 768px) {
    .main__item {
      font-size: 40px;
      line-height: 1.2; } }

* {
  box-sizing: border-box; }
